let _self
import compressImage from '../components/addImg/util.js';
const device = uni.getSystemInfoSync();
export default {
	data() {
		return {
			title:'',
			content:'',
			index:0,
			array:['专业','考研','就业'],
		    percent:0,
		    loading:false,
		    disabled:false,
			imageList: [],
			maxImgCount:1,
			addFlag:false,
			percent:0
		}
	},
	components:{
		
	},
	onLoad(e) {
		_self = this;
		//设置草稿数量初始化
	/* 	uni.clearStorage()
		console.log('清空了') */
		uni.getStorage({
			key:'draftList',
			success:function(res){
				/* console.log(res.data) */
			},
			fail:function(){
				uni.setStorage({
					key:'draftList',
					data:[],
					success:function(){
						console.log('新建了草稿列表')
					}
				})
			}
		})
	},
	methods: {
		typeSelect: function(e) {//类型选择
		   this.index = e.target.value
		},
		chooseImg() { //选择图片
			uni.chooseImage({
				sourceType: ["camera", "album"],
				sizeType: ['original'],
				count: 1,
				success: async (res) => {
					_self.imageList = res.tempFilePaths
					console.log(_self.imageList[0])
					_self.addFlag = true
					let up =  uni.uploadFile({
						url:'http://www.shenzepengzuishuai.cn/sso-file-service-0.0.1-SNAPSHOT/uploadImg',
						filePath: _self.imageList[0],
						// name:'draft',
						// fileType:'image',
						success:(uploadFileRes)=>{
							console.log(uploadFileRes)
						}
					})
					up.onProgressUpdate(function(res){
						_self.percent = res.progress
						console.log('上传进度' + res.progress)
						console.log('已经上传的数据长度' + res.totalBytesSent)
						console.log('预期需要上传的数据总长度' + res.totalBytesExpectedToSend)
					})
				}
			})
		},
		close(e) {
			this.imageList.splice(e, 1);
			this.addFlag = false
		},
		previewImage(index) { //预览图片
			uni.previewImage({
				urls: this.imageList,
				current: this.imageList[index]
			});
		},
		async compressImageHandler(src){
			console.log('platform==='+device.platform)
			const tempPath = await compressImage(src,device.platform);
			console.log('tempPath-----'+tempPath);
			return tempPath
		},
		save:function(){//存为草稿
			/* console.log(this.array[this.index]) */
			//将发表的草稿内容存到本地
			let draftData = {
				title:_self.title,
				type:_self.array[_self.index],
				content:_self.content,
				imgPath:_self.imageList[0]
			}
			uni.getStorage({
				key:'draftList',
				success:function(res){
					let data = res.data
					/* console.log(draftData) */
					let newDataLength = data.push(JSON.stringify(draftData))
					/* console.log(data) */
					console.log('现在有' + newDataLength+ '个草稿')
					uni.setStorage({
						key:'draftList',
						data:data,
						success:function(){
							uni.showToast({
								title:'保存成功'
							})
						}
					})
					
				},
				fail:function(){
					console.log('出错了')
					/* uni.setStorage({
						key:'draftList',
						data:[],
						success:function(){
							console.log('新建了草稿列表')
						}
					}) */
				}
			})
		},
		publish:function(){//发表话题
			uni.request({
				url:'http://47.97.213.36:7300/mock/5e145ffaf2ecc141ee032ab8/leader/publishFlag',
				method:'POST',
				data:{
					"title":_self.title,
					"type":_self.array[_self.index],
					"content":_self.content,
					"imgPath":_self.imageList[0]
				},
				success:function(res){
					if(res.data.flag == 1){
						uni.showToast({
							title:"等待审核"
						})
					}else{
						
					}
				}
			})
		}
	}
}
